.outer {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  left: 0;
  background-color: rgba(0, 0, 0, 0.692);
}
.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  height: 40vh;
  width: 75vw;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  border-radius: 20px;
  background-color: white;
  justify-content: center;
  align-items: center;
}
.loader {
  border: 14px solid #f3f3f3;
  border-top: 14px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-bottom: 40px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
h4 {
  font-size: 20px;
  padding-bottom: 10px;
}
.blank {
  height: 100px;
}
